import React, { Component } from 'react';
import Table from './Table';
import Form from './Form';
import stealy from './stealy.png';

class App extends Component {
  state = {
    shows: []
  };
  removeShows = index => {
    const { shows } = this.state;
    this.setState({
      shows: shows.filter((show, i) => {
        return i !== index;
      })
    });
  };

  handleSubmit = show => {
    this.setState({ shows: [...this.state.shows, show] });
  };

  render() {
    const { shows } = this.state;
    return (
      <div className="container">
        <img src={stealy} className="App-logo" alt="Spinning Stealy" />
        <h1>
          Enter The Date and Venue For All the Dead Shows You're Going to See!
        </h1>
        <Table showsData={shows} removeShows={this.removeShows} />
        <Form handleSubmit={this.handleSubmit} />
      </div>
    );
  }
}

export default App;
