import React, { Component } from 'react';

const TableHeader = () => {
  return (
    <thead>
      <tr>
        <th>Date</th>
        <th>Venue</th>
        <th>Remove</th>
      </tr>
    </thead>
  );
};

const TableBody = props => {
  const rows = props.showsData.map((row, index) => {
    return (
      <tr key={index}>
        <td>{row.date}</td>
        <td>{row.venue}</td>
        <td>
          <button onClick={() => props.removeShows(index)}>Delete</button>
        </td>
      </tr>
    );
  });
  return <tbody>{rows}</tbody>;
};

class Table extends Component {
  render() {
    const { showsData, removeShows } = this.props;

    return (
      <table>
        <TableHeader />
        <TableBody showsData={showsData} removeShows={removeShows} />
      </table>
    );
  }
}

export default Table;
